import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, GiaSpInfComp } from '../../../../models/dtos/mlf/giaSpInfComp';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const GiaSpInfCompSearchView: FC<SearchViewProps<GiaSpInfComp>> = props => {
  const columns: ColumnsProps[] = [
    referenceColumn({
      align: 'center',
      dataField: 'referencia',
      editable: false,
      headerAlign: 'center',
      sort: true,
      text: 'Referência',
    }),
  ];
  return (
    <WindowComponent title="Informações Complementares" wikiId={35}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'GiaSpInfCompEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default GiaSpInfCompSearchView;
